"use client";

import { useRouter } from "next/navigation";

const menuLinks = [
  { label: "Admin", path: "/editor" },
  { label: "Cidade", path: "/city" },
  { label: "Casa", path: "/house" },
  { label: "Fazenda", path: "/farm" },
  { label: "Floresta", path: "/forest" },
  { label: "Lojas", path: "/shops" },
  { label: "Chat", path: "/chat" }
] as const;

type BottomMenuProps = {
  variant?: "page" | "overlay";
};

export function BottomMenu({ variant = "page" }: BottomMenuProps = {}) {
  const router = useRouter();

  function handleNavigate(path: string) {
    if (typeof window !== "undefined") {
      window.localStorage.setItem("lastRoute", path);
      window.dispatchEvent(new CustomEvent("lastRouteChange", { detail: path }));
    }
    router.push(path);
  }

  return (
    <nav className={`bottom-menu${variant === "overlay" ? " bottom-menu--overlay" : ""}`}>
      {menuLinks.map((link) => (
        <button key={link.path} type="button" onClick={() => handleNavigate(link.path)}>
          {link.label}
        </button>
      ))}
    </nav>
  );
}
